<template>
	<div>
		<div class="box">
			<Head>
				<router-link to="/List" slot="left" tag="div">
					<img src="../../images/110.png" alt="">
				</router-link>

				<div slot="center">{{lost.name}}</div>
				<router-link to="/List" slot="right" tag="div">
					切换城市
				</router-link>
			</Head>
			<div class="content">
				<div class="inp">
					<input type="text" placeholder="输入学校，商务楼，地址" v-model="val">
					<button @click="sub">提交</button>
				</div>
				<div class="ser" @click="placehistory">
					搜索历史
				</div>
				<div class="zhong">
                  <span v-for="item in info" :key="item.id" @clcik="subNext(item)" v-show="isShow">
                      <router-link class="a" :to="{name:'Goods',params:{name:item.name}}">
                         {{item.name}}
                      </router-link>
                      <P>
                         {{item.address}}
                      </P>
                  </span>
					<!-- <hr /> -->
					<span v-for="(item,index) in searchlist" :key="index" @click="subNext(item)">
                        <router-link class="a" :to="{name:'Goods',params:{name:item.name}}">
                             {{item.name}}
                         </router-link>
                        <P>
                            {{item.address}}
                        </P>
                    </span>
					<div class="del" @click="del">清空所有</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Head from '../../components/Header.vue'
	import axios from 'axios'

	export default {
		components: {
			Head
		},
		data() {
			return {
				lost: '',
				val: '',
				searchlist: [],
				info: [],
				isShow: false
			}
		},
		mounted() {
			axios.get('http://elm.cangdu.org/v1/cities/' + this.$route.params.id).then(res => {
				//   console.log(res)
				this.lost = res.data;
			})
		},
		methods: {
			sub() {
				if (this.val) {
					axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id=' + this.$route.params.id + '&keyword=' + this.val)
						.then(res => {
							this.searchlist = res.data
						})
				}
			},
			subNext(item) {
				let check = false;
				this.info.forEach(i => {
					if (i.address == item.address) {
						check = true
					}
				});
				if (check == false) {
					this.info.push(item)
				}
				localStorage.setItem('history', JSON.stringify(this.info))
				//  this.$router.push({name:'Goods',params:{name:item.name}})
			},
			del() {
				this.info = []
				localStorage.removeItem('history', null)
			},
			placehistory() {
				this.isShow = true;
			}
		},
		created() {
			if (localStorage.getItem('history')) {
				this.info = JSON.parse(localStorage.getItem('history'))
			}
		}
	}
</script>

<style lang="less" scoped>
	.box {
		width: 100%;
		overflow: hidden;
		header {
			width: 100%;
			height: 0.8rem;
			background: #3792E5;
			div {
				color: white;
			}
			div:nth-of-type(1) {
				margin-left: 0.5rem;
				img {
					width: 0.3rem;
				}
			}
			div:nth-of-type(3) {
				margin-right: 0.5rem;
			}
		}
		.content {
			width: 100%;
			.inp {
				width: 100%;
				text-align: center;
				input {
					width: 90%;
					height: 0.8rem;
					font-size: 0.35rem;
					border: 1px solid #A4A4A4;
					margin-top: 0.3rem;
					margin-bottom: 0.3rem;
					outline: none;
				}
				button {
					width: 90.8%;
					height: 0.8rem;
					background: #3792E5;
					color: white;
					font-size: 0.4rem;
					border: none;
					margin-bottom: 0.3rem;
					outline: none;
				}
			}
			.ser {
				text-indent: 0.4rem;
				width: 100%;
				height: 0.5rem;
				line-height: 0.5rem;
				background: #F5F5F5;
				border: 0.04rem solid #E7E7E7;
				border-left: none;
				border-right: none;
				font-size: 0.24rem;
			}
			.zhong {
				width: 100%;
				.a {
					text-decoration: none;
					color: black;
					margin-left: 0.5rem;
					font-size: 0.4rem;
					font-weight: bold;
				}
				span {
					display: block;
					font-size: 0.4rem;
					font-weight: 530;
					border-bottom: 1px solid #A4A4A4;
				}
				p {
					display: block;
					font-size: 0.3rem;
					// margin-left: 1.9rem;
					margin-top: 0.4rem;
					margin-left: 0.5rem;
				}
				hr {
					margin-top: 0.6rem;
					color: #E4E4E4;
				}
				.del {
					text-align: center;
					color: #919191;
					font-size: 0.5rem;
					margin-top: 0.3rem;
					margin-bottom: 0.3rem;
				}
			}

		}
	}
</style>


